<template>
<div>
  <div v-if="!objOneObject" class="objAlign">Дождитесь загрузки объекта...</div>
  <div v-if="objOneObject">
    <!-- <div class="obj_title_subtitle_div"> -->
      <div class="obj_title_div" :style="objSmallTitle">
        <icon-6xright class="icon_6x"/>
            <div>
              <h2 class="obj_title">Шахта</h2>
            </div>
          <icon-dbl-line-title v-if="decor" class="icon_dbl_line_title"/>
      </div>
      <button @click="caveBigSwitch">Cave!</button>
      Tech:{{JSON.stringify(tech)}}{{this.loadedEquip}}
      Cargo:{{buksirCargo}}
      <br>
      <div class="box_box">
        <div class="oper_box">
          <div v-if="isMaster">
            <p  class="box_text">Вы мастер!</p>
            <hr>
          </div>
          <div v-if="skills.stim">
            <p class="box_text">Использован<br>стимлутятор!</p>
            <hr>
          </div>
          <p class="box_text">{{mySkill}}</p>
          <hr>
          <p class="box_text">Техника:</p>
          <b-dropdown :text="mySelectedTech.text" variant="secondary" class="eqp_drop" :key="mySelectedTech.text">
            <b-dropdown-item-button
              class="eqp_drop_item"
              v-for="item in myTechView"
              v-html="item.text"
              :key="item.text"
              @click="techSelect(item.id)"
            />
          </b-dropdown>
          <br>
          <div class="techAlign" v-if="mySelectedTech.id!=''">
            <button  class="btn btn-secondary marginTop" @click="test">Отпустить</button>
            <hr>
            <p class="box_text">{{techFunc}}</p>
            <hr>
            <p class="box_text">{{techFuel}}</p>

            <div class="techAlign" v-if="diggerWork">
              <hr>
              <p class="box_text">Месторождение:</p>
              <p class="box_text">{{isMine}}</p>
              <button  v-if="!caveGet.work" class="btn btn-secondary" @click="techDig">Копать!</button>
              <button  v-if="caveGet.work" class="btn btn-danger overtop" @click="techDigStop">Не копать!
              </button>
            </div>

            <div class="techAlign" v-if="buksirWork">
              <hr>
              <p class="box_text">Баржа в зоне досягаемости!</p>
              <button  v-if="!caveGet.work" class="btn btn-secondary" @click="techCatch">Стыковаться!</button>
              <button  v-if="caveGet.work" class="btn btn-danger overtop" @click="techDigStop">Отменить!</button>
            </div>

            <div class="techAlign" v-if="loaderWork">
              <hr>
              <p class="box_text">В зоне досягаемости:</p>
              <b-dropdown :text="goodsGrSelected" variant="secondary" class="eqp_drop" :key="goodsGrSelected">
                <b-dropdown-item-button
                  class="eqp_drop_item"
                  v-for="item in loaderWork"
                  v-html="item.text"
                  :key="item.text"
                  @click="goodsGrSelect(item.id)"
                />
              </b-dropdown>
              <button  v-if="goodsGrSelectedId && !caveGet.work" class="btn btn-secondary marginTop" @click="goodsTake()">Загрузить!</button>
              <button  v-if="caveGet.work" class="btn btn-danger overtop" @click="techDigStop">Остановить!</button>
            </div>

            <div class="techAlign" v-if="loaderCargo">
              <hr>
              <p class="box_text">Лежит в трюме:</p>
              <b-dropdown :text="goodsCargoSelected" variant="secondary" class="eqp_drop" :key="goodsCargoSelected">
                <b-dropdown-item-button
                  class="eqp_drop_item"
                  v-for="item in loaderCargo"
                  v-html="item.text"
                  :key="item.text"
                  @click="goodsCargoSelect(item.id)"
                />
              </b-dropdown>
              <button  v-if="goodsCargoSelectedId && !caveGet.work" class="btn btn-secondary marginTop" @click="goodsUnload()">Выгрузить!</button>
              <button  v-if="caveGet.work" class="btn btn-danger overtop" @click="techDigStop">Остановить!</button>
            </div>

            <div class="techAlign" v-if="buksirCargo">
              <hr>
              <p class="box_text">Кажется что-то лишнее...</p>
              <button  v-if="!caveGet.work" class="btn btn-secondary" @click="goodsEject">Выбросить!</button>
              <button  v-if="caveGet.work" class="btn btn-danger overtop" @click="techDigStop">Отменить!</button>
            </div>

            <div class="techAlign" v-if="dronWork">
              <hr>
              <p class="box_text">Проведено циклов исследования: {{this.cycles}}</p>
              <button  v-if="!caveGet.work" class="btn btn-secondary" @click="resStart">Включить!</button>
              <button  v-if="caveGet.work" class="btn btn-danger overtop" @click="techDigStop">Выключить!</button>
            </div>

          </div>
        </div>

        <div class="cave_box">
          <table class="horiz_nums">
            <tr>
              <td v-for="ldig in caveCol(caveX)"><p class="hnum">{{ldig}}</p></td>
            </tr>
          </table>
          <table class="vert_nums">
            <tr v-for="lrow in caveRow(caveZ)">
              <td ><p class="vnum">{{lrow}}</p></td>
            </tr>
          </table>

          <table  class="cave_table">
            <tr v-for="lrow in caveRow(caveZ)">
              <td v-for="ldig in caveCol(caveX)">
                <s-cell
                  :cell="cellElem(lrow,ldig)"
                  :equip="cellEquip(lrow,ldig)"
                  clSize="150px"
                  :key="cellKey(lrow,ldig)"/>
              </td>
            </tr>
          </table>
          <div class="caveUp" @click="moveUp"><s-arr class="arr_up"/></div>
          <div class="caveDn" @click="moveDn"><s-arr class="arr_dn"/></div>
          <div class="caveLf" @click="moveLf"><s-arr class="arr_lf"/></div>
          <div class="caveRt" @click="moveRt"><s-arr class="arr_rt"/></div>

        </div>
      </div>
      <!-- <div v-if="caveGet.work" class="cave_main" @click.stop=""> -->

      </div>
      <main-cave v-if="caveBigShow"  @done="caveBigSwitch" :cave="cave"/>
  </div>
</div>
</template>
<script>
import {
  mapGetters
} from 'vuex';
import {
  mapMutations
} from 'vuex';
import {
  mapActions
} from 'vuex';

import {
  convSet
} from './stringlib.js';
const conv = convSet();

import right6x from '../assets/6x_right.svg';
import arrow from '../assets/single-arr-left.svg';
import dblLinesTitle from '../assets/double-lines-title.svg';

import mainCave from './cave.vue';
import caveCell from './cavecell.vue';

export default {
  props: ['objid','decor','small'],
  components: {
    'icon-6xright': right6x,
    'icon-dbl-line-title': dblLinesTitle,
    'main-cave':mainCave,
    's-arr':arrow,
    's-cell':caveCell,
  },
  data() {
    return {
        caveBigShow:false,
        actStat:false,
        actid: false,
        obj: this.objid,
        wrnStat: false,
        wrnMessage:"",
        defX:10,
        defZ:10,
        goodsGrSelectedId:false,
        goodsCargoSelectedId:false,
        cycles:0
    }
  },
  created(){

  },
  methods: {
    ...mapActions(['sendActDirect']),
        ...mapMutations(['caveSet']),
    caveBigSwitch(){this.caveBigShow=!this.caveBigShow;},
    objActSwitch(actName, event) {
      event.stopPropagation();
      // this.actid = actName;
      // if ("user_params" in this.objOneObject.actions[actName]) {
      //   router.beforeEach((to, from, next) => {
      //     next(false);
      //   });
      //   this.actStat = true;
      // } else {
      //   if("warning" in this.objOneObject.actions[actName]){
      //     router.beforeEach((to, from, next) => {
      //       next(false);
      //     });
      //     this.wrnMessage=this.objOneObject.actions[actName].warning;
      //     this.wrnStat=true;
      //   }
      //   else{
      //     this.sendAct({actid:this.actid,obj:this.objOneObject});
      //     this.actid = false;
      //     this.actStat = false;
      //   }
      // }
      // return false;
    },
    moveUp(){
      if(this.mySelectedTech.id=="") return false;
      if(!this.isOk) return false;
      if(this.caveGet.work) return false;
      let litem=this.equip[this.mySelectedTech.id];

      if(litem.type=="ship" || litem.type=="buksir" || litem.type=="stealer" || litem.type=="barge"){
        if(this.caveZ-1 <0) return false;
        if(this.caveZ-1 <59 && this.caveX > 10 && this.caveX <59)  return false;
      }
      else if(this.caveZ-1 <10) return false;
      if(this.cellElem(this.caveZ,this.caveX).indexOf("t")>=0) return false;
      this.techMove(this.caveX,this.caveZ-1);
//      this.caveZ=this.caveZ-1
    },
    moveDn(){
      if(this.mySelectedTech.id=="") return false;
      if(!this.isOk) return false;
      if(this.caveGet.work) return false;
      let litem=this.equip[this.mySelectedTech.id];
      if(litem.type=="ship" || litem.type=="buksir" || litem.type=="stealer" || litem.type=="barge"){
        if(this.caveZ+1 >69) return false;
        if(this.caveZ+1 >10 && this.caveX > 10 && this.caveX <59)  return false;
      }
      else if(this.caveZ+1 >59) return false;
      if(this.cellElem(this.caveZ,this.caveX).indexOf("b")>=0) return false;
      this.techMove(this.caveX,this.caveZ+1);
        //this.caveZ=this.caveZ+1
    },
    moveLf(){
      if(this.mySelectedTech.id=="") return false;
      if(!this.isOk) return false;
      if(this.caveGet.work) return false;
      let litem=this.equip[this.mySelectedTech.id];
      if(litem.type=="ship" || litem.type=="buksir" || litem.type=="stealer" || litem.type=="barge"){
        if(this.caveX-1 <0) return false;
        if(this.caveX-1 <59 && this.caveZ > 10 && this.caveZ <59)  return false;
      }
      else if(this.caveX-1 <10) return false;
      if(this.cellElem(this.caveZ,this.caveX).indexOf("l")>=0) return false;
      this.techMove(this.caveX-1,this.caveZ);
      // this.caveX=this.caveX-1
    },
    moveRt(){
      if(this.mySelectedTech.id=="") return false;
      if(!this.isOk) return false;
      if(this.caveGet.work) return false;
      let litem=this.equip[this.mySelectedTech.id];
      if(litem.type=="ship" || litem.type=="buksir" || litem.type=="stealer" || litem.type=="barge"){
        if(this.caveX+1 >69) return false;
        if(this.caveX+1 >10 && this.caveZ > 10 && this.caveZ <59)  return false;
      }
      else if(this.caveX+1 >59) return false;
      if(this.cellElem(this.caveZ,this.caveX).indexOf("r")>=0) return false;
      this.techMove(this.caveX+1,this.caveZ);
//      this.caveX=this.caveX+1
    },
    cellEquip(lrow,ldig){
      let leqp=[];
      let lstone=false;
      let lbrill=false;
      let liron=false;
      let lbox=false;

      for( let lkey in this.equip){
        if(this.loadedEquip.indexOf(lkey)<0)
        {
          let item=this.equip[lkey];
          if(lkey == this.mySelectedTech.id)
          {
            item.selected=true;
            item.work=this.caveGet.work;
          }
          if(item.x==ldig && item.y==lrow )
          {
            if(item.type=="stone") {
              if(lstone) lstone.number=lstone.number+1;
              else {
                lstone=Object.assign({},item);
                lstone["number"]=1;
              }
            }
            else
            if(item.type=="iron") {
              if(liron) liron.number=liron.number+1;
              else {
                liron=Object.assign({},item);
                liron["number"]=1;
              }
            }
            else
            if(item.type=="brill") {
              if(lbrill) lbrill.number=lbrill.number+1;
              else {
                lbrill=Object.assign({},item);
                lbrill["number"]=1;
              }
            }
            else
            if(item.type=="box") {
              if(lbox) lbox.number=lbox.number+1;
              else {
                lbox=Object.assign({},item);
                lbox["number"]=1;
              }
            }
            else leqp.push(item);
          }
        }
      }
      if(lstone) leqp.push(lstone);
      if(liron) leqp.push(liron);
      if(lbrill) leqp.push(lbrill);
      if(lbox) leqp.push(lbox);

      return leqp;
    },
    cellGoods(lx,ly){
      let leqp={};
      let len=0;
      for( let lkey in this.equip){
        if(this.equip[lkey].x==lx && this.equip[lkey].y==ly)
        {
          if(this.equip[lkey].type=="box" || this.equip[lkey].type=="brill" || this.equip[lkey].type=="iron" || this.equip[lkey].type=="stone")
          {
            if(this.loadedEquip.indexOf(lkey)<0)
            {
              len++;
              leqp[lkey]=this.equip[lkey];
            }
          }
        }
      }
      if(len>0) return Object.assign({},leqp);
      else return false;
    },
    cellElem(lrow,ldig)
    {
      if(lrow<0 || lrow>=this.cave.length) return ["unknown"];
      if(ldig<0 || ldig>=this.cave[0].length) return ["unknown"];

      let l_elem=[].concat(this.cave[lrow][ldig]);
      if(l_elem.indexOf("sp")<0)
      {
        if(lrow<this.caveZ-this.sumSkill || lrow>this.caveZ+this.sumSkill) return ["unknown"];
        if(ldig<this.caveX-this.sumSkill || ldig>this.caveX+this.sumSkill) return ["unknown"];
      }

      if(l_elem.indexOf("l")<0 && ldig>0 && this.cave[lrow][ldig-1].indexOf("r")>=0) l_elem.push("l");
      if(l_elem.indexOf("r")<0 && ldig<this.cave[0].length-1 && this.cave[lrow][ldig+1].indexOf("l")>=0) l_elem.push("r");
      if(l_elem.indexOf("t")<0 && lrow>0 && this.cave[lrow-1][ldig].indexOf("b")>=0) l_elem.push("t");
      if(l_elem.indexOf("b")<0 && lrow<this.cave.length-1 && this.cave[lrow+1][ldig].indexOf("t")>=0) l_elem.push("b");

      return l_elem;

    },
    cellKey(lrow,ldig)
    {
      return lrow.toString()+"_"+ldig.toString()+"_"+Math.random().toString();
    },
    caveCol(lx)
    {
      return [lx-2,lx-1,lx,lx+1,lx+2];
    },
    caveRow(lz)
    {
      return [lz-2,lz-1,lz,lz+1,lz+2];
    },
    caveCreate(lcave){
      let lsprow=[];
      var thiscave=[];
      for(let l=0;l<70;l++)
      {
        lsprow.push(["sp"]);
      }
      let lspside=[];
      for(let l=0;l<10;l++)
      {
        lspside.push(["sp"]);
      }

      for(let l=0;l<10;l++)
      {
        thiscave.push(lsprow);
      }

      let ltmp=lcave.split("|");
      let ltmp2=[];
      ltmp.forEach((item)=>{ltmp2.push(item.split(" "))});

      for(let l=0;l<50;l++)
      {
        thiscave.push([].concat(lspside,ltmp2.slice(l*50,l*50+50),lspside));
      }
      for(let l=0;l<10;l++)
      {
        thiscave.push(lsprow);
      }
      return thiscave;
    },
    techNames(lname){
      let ltech={
        driller:"Бурильщик",
        box:"Коробки",
        brill:"Алмазы",
        brillmine:"Шахта алмазов",
        buksir:"Буксир",
        dron:"Дрон",
        digger:"Экскаватор",
        iron:"Иридий",
        ironmine:"Шахта иридия",
        loader:"Погрузчик",
        stealer:"Попрыгун",
        stone:"Руда",
        stonemine:"Шахта руды",
        ship:"Корабль",
        barge:"Баржа",
        train:"Поезд"
      }
      return ltech[lname];
    },
    test(){
      if(this.caveGet.work) return false;
      if(this.caveGet.move) return false;
      this.techRelease();
    },
    techSelect(litem){
      if(this.mySelectedTech.id!="") return false;
      let lpayload={
        code: "take_equipment",
        params:{},
        user_params:{id:litem}
      }
      this.sendActDirect(lpayload);
    },
    goodsGrSelect(litem){
      if(!this.caveGet.work) this.goodsGrSelectedId=litem;
    },
    goodsCargoSelect(litem){
      if(!this.caveGet.work) this.goodsCargoSelectedId=litem;
    },
    techRelease(){
      let lpayload={
        code: "free_equipment",
        params:{},
        user_params:{id:this.mySelectedTech.id}
      }
      this.sendActDirect(lpayload);
    },
    eqpAdd(ltype){
      let lpayload={
        code: "add_equipment",
        params:{},
        user_params:{x:this.caveX,y:this.caveZ,type:ltype}
      }
      this.sendActDirect(lpayload);
    },
    techAddContent(lcont){
      let lpayload={
        code: "add_content",
        params:{},
        user_params:{id:this.mySelectedTech.id,content_id:lcont}
      }
      this.sendActDirect(lpayload);
    },
    techRemContent(lid,lcont){
      let lpayload={
        code: "remove_content",
        params:{},
        user_params:{id:lid,content_id:lcont}
      }
      this.sendActDirect(lpayload);
    },
    techMove(lx,ly){
      let lpayload={
        code: "move_equipment",
        params:{},
        user_params:{x:lx,y:ly,id:this.mySelectedTech.id}
      }
      this.sendActDirect(lpayload);
    },
    digIt(){
      return setTimeout(() => {
        let lret=""
        this.cellEquip(this.caveZ,this.caveX).forEach((item)=>{
          if(item.type=="stonemine") lret="stone";
          if(item.type=="ironmine") lret="iron";
          if(item.type=="brillmine") lret="brill";
        });
        if(lret!="" && this.caveGet.work){
          this.eqpAdd(lret);
//          this.caveSet({work:false,timeout:false});
          this.caveSet({work:true,timeout:this.digIt()});
        }
        else{
          this.caveSet({work:false,timeout:false});
        }
      }, 120000);
    },
    takeIt(){
      return setTimeout(() => {
        if(this.goodsGrSelectedId!=false && this.caveGet.work){
          this.techAddContent(this.goodsGrSelectedId);
          this.caveSet({work:false,timeout:false});
          this.goodsGrSelectedId=false;
        }
        else{
          this.caveSet({work:false,timeout:false});
        }
      }, 30000);
    },
    catchIt(){
      return setTimeout(() => {
        if(this.buksirWork!=false && this.caveGet.work){
          this.techAddContent(this.buksirWork);
          this.caveSet({work:false,timeout:false});
        }
        else{
          this.caveSet({work:false,timeout:false});
        }
      }, 30000);
    },
    unloadIt(){
      return setTimeout(() => {
        if(this.goodsCargoSelectedId!=false && this.caveGet.work){
          this.techRemContent(this.mySelectedTech.id,this.goodsCargoSelectedId);
          this.caveSet({work:false,timeout:false});
          this.goodsCargoSelectedId=false;
        }
        else{
          this.caveSet({work:false,timeout:false});
        }
      }, 30000);
    },
    ejectIt(){
      return setTimeout(() => {
        if(this.buksirCargo!=false && this.caveGet.work){
          this.buksirCargo.forEach((item)=>{
            this.techRemContent(this.equip[this.mySelectedTech.id].content[0], item);
          });
          this.caveSet({work:false,timeout:false});
        }
        else{
          this.caveSet({work:false,timeout:false});
        }
      }, 30000);
    },
    researchIt(){
      return setTimeout(() => {
        if(this.dronWork!=false && this.caveGet.work){
          this.cycles++;
          this.caveSet({work:true,timeout:this.researchIt()});
        }
        else{
          this.caveSet({work:false,timeout:false});
        }
      }, 3000);
    },
    goodsEject(){
      if(!this.caveGet.work)
        this.caveSet({work:true,
          timeout: this.ejectIt()
        });
    },
    resStart(){
      if(!this.caveGet.work)
        this.caveSet({work:true,
          timeout: this.researchIt()
        });
    },
    goodsUnload(){
      if(!this.caveGet.work)
        this.caveSet({work:true,
          timeout: this.unloadIt()
        });
    },
    techCatch(){
      if(!this.caveGet.work)
        this.caveSet({work:true,
          timeout: this.catchIt()
        });
    },
    goodsTake(){
      if(!this.caveGet.work)
        this.caveSet({work:true,
          timeout: this.takeIt()
        });
    },
    techDig(){
      this.caveSet({work:true,
        timeout: this.digIt()
      });
    },
    techDigStop(){
      if(this.caveGet.timeout) clearTimeout(this.caveGet.timeout);
      this.caveSet({work:false,timeout:false});
    }
  },
  computed: {
    ...mapGetters(['caveGet',"actorsGet"]),
    objSmallTitle(){
      if(this.small){
        return "padding-left:25px;"
      }
      return "";
    },
    objOneObject() {
      if(this.obj){
        if('object' in  this.obj){
          return this.obj.object;
        }
        else return this.obj;}
      else return false;
    },
    objListObject() {
      if ('list' in this.obj){
        if ('objects' in this.obj.list)
          return this.obj.list.objects;
      }
      return false;
    },
    cave(){
      var lcave=[];
        this.objListObject.forEach((item)=>{
          if(item.id === "mine_map"){
            lcave=this.caveCreate(item.attributes.mine_map.value);
          }
        });
        return lcave;
    },
    skills(){
      let lperson={};
      this.objListObject.forEach((item)=>{
          if(item.id === "mine_person"){
            lperson["skill"]=item.attributes["miner_skill"].value;
            lperson["stim"]=item.attributes["is_stimulant_used"].value;
          }
        });
        return lperson;
    },
    myTechView(){
      let lret=[];
      let lnum=0;
      for(let lkey in this.myTech)
      {
        lnum++;
        lret.push({id: lkey, text:lnum+": "+this.techNames(this.myTech[lkey].type)+" X: " +this.myTech[lkey].x+" Y: " +this.myTech[lkey].y});
      }
      return lret;
    },
    myTech(){
      let llist={};
      for(let lkey in this.equip)
      {
        this.companies.forEach((item)=>{
          if(item == this.equip[lkey].firm) llist[lkey]=Object.assign({},this.equip[lkey]);
        });
      }
      let lllist={};
      for(let lkey in llist)
      {
        this.tech.forEach((item)=>{
          if(item == llist[lkey].type) lllist[lkey]=Object.assign({},llist[lkey]);
        });
      }
      let llllist={};
      for(let lkey in lllist)
      {
          if(lllist[lkey].actor_id == false || lllist[lkey].actor_id == this.actorsGet.active) llllist[lkey]=Object.assign({},lllist[lkey]);
      }
      return llllist;
    },
    mySelectedTech(){
      for(let lkey in this.myTech)
      {
        if(this.myTech[lkey].actor_id==this.actorsGet.active)
        {
          return Object.assign({},{id:lkey, text:"X: " +this.myTech[lkey].x+" Y: " +this.myTech[lkey].y+" "+ this.techNames(this.myTech[lkey].type)});
        }
      }
      this.techDigStop();
      this.goodsGrSelectedId=false;
      this.cycles=0;
      return Object.assign({},{id:"", text:"Выбрать..."});
    },
    goodsGrSelected(){
      if(this.goodsGrSelectedId) return this.techNames(this.equip[this.goodsGrSelectedId].type);
      else return "Выбрать..."
    },
    goodsCargoSelected(){
      if(this.goodsCargoSelectedId) return this.techNames(this.equip[this.goodsCargoSelectedId].type);
      else return "Выбрать..."
    },
    techFunc(){
      if(this.equip[this.mySelectedTech.id].broken===false) return "Исправно!"
      else return "Сломано."
    },
    techFuel(){
      if(this.equip[this.mySelectedTech.id].fueled===true) return "Заправлено!"
      else return "Нет топлива."
    },
    isOk(){
      if(this.equip[this.mySelectedTech.id].broken===false && this.equip[this.mySelectedTech.id].fueled===true) return true;
      else
      {
        this.techDigStop();
        return false;
      }
    },
    diggerWork(){
      let lmine=["stonemine","ironmine","brillmine"];
      let lret=false;
      if(this.mySelectedTech.id!="")
        if(this.isOk)
          if(this.equip[this.mySelectedTech.id].type=="digger")
          {
            this.cellEquip(this.caveZ,this.caveX).forEach((item)=>{
              if(lmine.indexOf(item.type)>=0) lret=true;
            })
          }
      return lret;
    },
    buksirWork(){
      if(this.mySelectedTech.id!="")
        if(this.isOk)
          if(this.equip[this.mySelectedTech.id].type=="buksir")
          for(let lkey in this.equip)
          {
              if(this.equip[lkey].type=="barge")
                if(this.equip[lkey].x==this.caveX && this.equip[lkey].y==this.caveZ)
                  if(this.loadedEquip.indexOf(lkey)<0) return lkey;
          }
      return false;
    },
    buksirCargo(){
      if(this.mySelectedTech.id!="")
        if(this.isOk)
          if(this.equip[this.mySelectedTech.id].type=="buksir")
            if(this.equip[this.mySelectedTech.id].content.length>0)
              if(this.equip[this.equip[this.mySelectedTech.id].content[0]].content.length>0)
                return this.equip[this.equip[this.mySelectedTech.id].content[0]].content;
      return false;
    },
    dronWork(){
      if(this.mySelectedTech.id!="")
        if(this.isOk)
          if(this.equip[this.mySelectedTech.id].type=="dron" || this.equip[this.mySelectedTech.id].type=="ship" || this.equip[this.mySelectedTech.id].type=="driller" )
            return true;
      return false;
    },
    loaderWork(){
      if(this.mySelectedTech.id!="")
        if(this.isOk)
          if(this.equip[this.mySelectedTech.id].type=="loader" || this.equip[this.mySelectedTech.id].type=="ship")
          {
            let leqp=this.cellGoods(this.caveX,this.caveZ);
            let lfind=true;

            let lret=[];
            let lnum=0;
            if(leqp)
            {
              for(let lkey in leqp)
              {
                lnum++;
                lret.push({id:lkey, text:lnum+": " + this.techNames(leqp[lkey].type)});
                if(lkey==this.goodsGrSelectedId) lfind=false;
              }
            }
            if(lfind) this.goodsGrSelectedId=false;
            if(lret.length>0) return lret;
            else return false;
          }
      return false;
    },
    loaderCargo(){
      let leqp=this.equip[this.mySelectedTech.id].content;
      if(this.goodsCargoSelectedId)
        if(leqp.indexOf(this.goodsCargoSelectedId)<0)
          this.goodsCargoSelectedId=false;

      let lret=[];
      let lnum=0;
      leqp.forEach((item)=>{
        lnum++;
        lret.push({id: item,text:lnum+": "+this.techNames(this.equip[item].type)});
      });

      if (leqp.length>0) return lret;
      else return false;
    },
    isMine(){
      var lret="";
      if(this.mySelectedTech.id!="")
        if(this.isOk)
          if(this.equip[this.mySelectedTech.id].type=="digger")
          {
            this.cellEquip(this.caveZ,this.caveX).forEach((item)=>{

              if(item.type=="stonemine") lret="руда";
              if(item.type=="ironmine") lret="иридий";
              if(item.type=="brillmine") lret="алмазы";
            });
          }
      return lret;
    },
    caveX(){
      if(this.mySelectedTech.id =="") return this.defX;
      return  this.equip[this.mySelectedTech.id].x;
    },
    caveZ(){
      if(this.mySelectedTech.id =="") return this.defZ;
      return  this.equip[this.mySelectedTech.id].y;
    },
    mySkill(){
      if(this.skills.skill==0) return "Ваш опыт: нормальный."
      else return "Ваш опыт: высокий!"
    },
    sumSkill(){
      let l=this.skills.skill;
      if(this.skills.stim) l=l+1;
      return l;
    },
    isMaster(){
      return this.objOneObject.attributes["is_master"].value;
    },
    companies(){
      return this.objOneObject.attributes["mine_available_companies"].value;
    },
    tech(){
      return this.objOneObject.attributes["mine_available_equipment"].value;
    },
    equip(){
      var leqp;
      this.objListObject.forEach((item)=>{
        if(item.id === "mine_equipment"){
          leqp=Object.assign({},item.attributes.mine_equipment.value);
        }
      });
      for(let lkey in leqp)
      {
        leqp[lkey]["empty"]=false;
        leqp[lkey]["selected"]=false;
        leqp[lkey]["work"]=false;
        leqp[lkey]["move"]=false;
      }
      return leqp;
  //    console.log(JSON.stringify(this.equip));
    },
    loadedEquip(){
      let lret=[];
      for(let lkey in this.equip)
      {
        if(this.equip[lkey].content.length>0) lret=lret.concat(this.equip[lkey].content);
      }
      return lret;
    },
    objAttributes() {
      if ('attributes' in this.objOneObject) return this.objOneObject.attributes;
      else return false;
    },
    objActions() {
      if ('actions' in this.objOneObject) return this.objOneObject.actions;
      else return false;
    }
  }
}
</script>
